import {BiGlobe} from 'react-icons/bi'
import {RiUserFill, RiUserAddFill} from 'react-icons/ri'

export default function Header(){
    return(
        <div className="bg-brand-color">
            <div className="container mx-auto h-16 md:h-11 flex items-center justify-center md:justify-between">
                <a href="#">
              <img src="data:image/svg+xml;base64," alt="Getir" />
                </a>

                <nav className="hidden md:flex gap-x-8 text-sm font-semibold">
                    <a href="#" className="flex items-center gap-x-2 text-white transition-all font-opacity-80 hover:text-opacity-30">
                        <BiGlobe size={20}/>
                        Türkçe (TR)
                    </a>
                    <a href="#" className="flex items-center gap-x-2 text-white transition-all font-opacity-80 hover:text-opacity-30">
                        <RiUserFill size={20}/>
                        Giriş Yap
                    </a>
                    <a href="#" className="flex items-center gap-x-2 text-white transition-all font-opacity-80 hover:text-opacity-30">
                        <RiUserAddFill size={20}/>
                        Kayıt Ol
                    </a>
                </nav>

            </div>
        </div>
    )
}
